
import React, { useContext, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { FirebaseContext } from '../context/firebase';
import { Form } from "../components";
import { HeaderContainer } from '../containers/header';
import { FooterContainer } from '../containers/footer';
import * as ROUTES from '../constants/routes';


export default function  SignUp() {
   const { firebase } = useContext(FirebaseContext);
  const history = useHistory();
    
    //     
   

    const [firstName, setFirstName] = useState('');

    //  
    const [emailAddress, setEmailAddress] = useState('');
    const [password, setPassword] = useState('');

    //    
    const [emailErrorMessage, setEmailErrorMessage] = useState('');
    
    //  6 
    const [passwordErrorMessage, setPasswordErrorMessage] = useState('');
  

  
    //    &&     
   //  const isInvalid = emailAdress === '' || password === '';
 
    const handleEmailChange = (event) => {
       setEmailErrorMessage('');
        setEmailAddress(event.target.value);
    }
    const handlePasswordChange = (event) => {

      setPasswordErrorMessage('');
       setPassword(event.target.value);
    }
    
    const handleSignup = (event) => {
       event.preventDefault();

       //   auth  Email Password  
       //  history  Router  Browse  
       return firebase
         .auth()
         .createUserWithEmailAndPassword(emailAddress,password)
         .then((result) =>
            result.user
           .updateProfile({
             displayName: firstName,
             photoURL: Math.floor(Math.random() * 5) + 1,
           })
           .then(() => {
             history.push(ROUTES.BROWSE);
           })
       )
      .catch((error) => { 
             //        
             setFirstName('');
             setEmailAddress('');
             setPassword('');

            if (error.code === 'auth/email-already-in-use') {
               //   
               setEmailErrorMessage('   .     ');
            } else if (error.code === 'auth/invalid-email') {
               //   
               setEmailErrorMessage('    .');
            } else if (error.code === 'auth/weak-password') {
               // 6   
               setPasswordErrorMessage(' 6  .');
            }
         })        
   
    }

    

    //   
    return (
       <>
         <HeaderContainer>
            <Form>
              <Form.Title> </Form.Title>
              {/*    */}
       
              {/* {passwordErrorMessage && <Form.Error data-testid="error">
                 {passwordErrorMessage}
              </Form.Error>} */}

              <Form.Base onSubmit={handleSignup} method="POST">
                  {/* <Form.Input
                  placeholder=" "
                  value={firstName}
                  onChange={({ target }) => setFirstName(target.value)}
               
                  /> */}
              
                 <Form.Input 
                    placeholder="    "  
                    value={emailAddress}
                    onChange={handleEmailChange}
                    hasError={emailErrorMessage}
                  //   onChange={({ target }) => setEmailAddress(target.value)} 
                    />
               {emailErrorMessage && <Form.Error>{emailErrorMessage}</Form.Error>}
                <Form.Input 
                   type="password"
                   value={password}
                   onChange={handlePasswordChange}
                   hasError={passwordErrorMessage}
                   autoComplate='off'
                   placeholder=""
                  //  onChange={({ target }) => setPassword(target.value)}
                  /> 
              {passwordErrorMessage && <Form.Error>{passwordErrorMessage}</Form.Error>}
                <Form.Submit type="submit"   data-testid="sign-up">
                   
                </Form.Submit>
              </Form.Base>
              <Form.Text>
                Netflix  ? <Form.Link to={ROUTES.SIGN_UP}> </Form.Link>
              </Form.Text>
              <Form.Text>
                    Google reCAPTCHA      . <Form.AboutLink href=""> </Form.AboutLink>
              </Form.Text>
            </Form>
         </HeaderContainer>
            <FooterContainer/>
       </>  
    )
}

 